<template>
  <div class="container">
    <h2 class="text-center">測驗開始</h2>
    <!-- 訊息提示框 -->
    <Alert :MODE="MSG.MODE" :MSGTYPE="MSG.TYPE" :TITLE="MSG.TITLE" :TEXT="MSG.TEXT" @alert-check="(sure) => (alertCheck = sure)" />
    <div class="row">
      <div class="col-12">
        <table v-if="this.examData.TFQ.length !== 0">
          <tr>
            <td colspan="2" class="h2 text-center">是非</td>
          </tr>
          <tr>
            <td class="h4">#</td>
            <td class="h4">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.TFQ" :id="item.QID" :style="index % 2 == 0 ? 'background-color: #d3fcff;' : 'background-color: #cfffcf;'">
            <td class="fw-bolder">{{ index + 1 }}</td>
            <td>
              <select :id="'tf' + index" v-model="item.answer">
                <option value=""></option>
                <option value="O">O</option>
                <option value="X">X</option>
              </select>
              <label class="fw-bold d-inline">{{ item.content }}</label>
            </td>
          </tr>
        </table>
        <table v-if="this.examData.SCQ.length !== 0">
          <tr>
            <td colspan="2" class="h2 text-center">單選</td>
          </tr>
          <tr>
            <td class="h4">#</td>
            <td class="h4">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.SCQ" :id="item.QID" :style="index % 2 == 0 ? 'background-color: #d3fcff;' : 'background-color: #cfffcf;'">
            <td>{{ index + 1 }}</td>
            <td>
              <p class="fw-bold">{{ item.content }}</p>
              <div v-for="(option, i) in item.options" class="form-check form-check-inline p-0">
                <input class="d-inline" type="radio" :name="'s' + index" :id="'s' + index + i" :value="item.options[i]" v-model="item.answer" />
                <label class="d-inline" :for="'s' + index + i">{{ item.options[i] }}</label>
              </div>
            </td>
          </tr>
        </table>
        <table v-if="this.examData.MCQ.length !== 0">
          <tr>
            <td colspan="2" class="h2 text-center">多選</td>
          </tr>
          <tr>
            <td class="h3">#</td>
            <td class="h3">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.MCQ" :id="item.QID" :style="index % 2 == 0 ? 'background-color: #d3fcff;' : 'background-color: #cfffcf;'">
            <td>{{ index + 1 }}</td>
            <td>
              <p class="fw-bold">{{ item.content }}</p>
              <div v-for="(option, i) in item.options" class="form-check form-check-inline p-0">
                <input class="d-inline" type="checkbox" :name="'m' + index" :id="'m' + index + i" :value="item.options[i]" v-model="item.answer" />
                <label class="d-inline" :for="'m' + index + i">{{ item.options[i] }}</label>
              </div>
            </td>
          </tr>
        </table>
      </div>
      <div class="col-6 d-grid gap-2 my-2">
        <button class="btn btn-secondary btn-lg" type="button" @click="$router.push('/examLobby')">返回大廳</button>
      </div>
      <div class="col-6 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="sendResult">我考完了！</button>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "Exam",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      examData: {
        TFQ: [],
        SCQ: [],
        MCQ: [],
      },
    };
  },
  methods: {
    sendResult() {
      // 訊息提示框
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.MSG.TYPE = "primary";
      this.MSG.TITLE = "確認交卷";
      this.MSG.TEXT = ["學長你要確餒!"];
    },
  },
  mounted() {
    // 取得考試資料
    this.examData = this.$store.state.examination;
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val) {
        // 核對TFQ答案
        this.examData.TFQ.forEach((item) => {
          // 比對答案
          if (item.answer === item.correct) {
            item.result = true;
          }
          // 計算加權分數weight，如果answer為空值+0，錯誤-1，若examData.isRandom為true，則正確+1.2，否則+0.7
          item.weight = item.answer === "" ? 0 : item.answer === item.correct ? (this.examData.isRandom ? 1.2 : 0.7) : -1;
        });
        // 核對SCQ答案
        this.examData.SCQ.forEach((item) => {
          // 在options中找出answer的index
          item.answerIndex = item.options.indexOf(item.answer) + 1;
          if (item.answerIndex === 0) {
            item.answerIndex = "";
          }
          // 比對答案
          if (item.answer === item.correct) {
            item.result = true;
          }
          // 計算加權分數weight，如果answer為空值+0，錯誤-1，若examData.isRandom為true，則正確+1.2，否則+0.7
          item.weight = item.answer === "" ? 0 : item.answer === item.correct ? (this.examData.isRandom ? 1.2 : 0.7) : -1;
        });
        // 核對MCQ答案
        this.examData.MCQ.forEach((item) => {
          // 在options中找出answer的index
          item.answerIndex = [];
          item.answer.forEach((ans) => {
            item.answerIndex.push(item.options.indexOf(ans) + 1);
          });
          item.answerIndex.sort((a, b) => a - b);
          // 比對答案
          if (item.answer.sort().toString() === item.correct.sort().toString()) {
            item.result = true;
          }
          // 計算加權分數weight，如果answer為空值+0，錯誤-1，若examData.isRandom為true，則正確+1.2，否則+0.7
          item.weight =
            item.answer.length === 0 ? 0 : item.answer.sort().toString() === item.correct.sort().toString() ? (this.examData.isRandom ? 1.2 : 0.7) : -1;
        });

        this.alertVisible = false; // 關閉訊息提示框
        // 取出TFQ、SCQ、MCQ的QID、weight，並傳送到後端
        const examResultSent = {
          exam_QGID_list: this.examData.exam_QGID_list,
          TFQ: this.examData.TFQ.map((item) => {
            return { QID: item.QID, weight: item.weight };
          }),
          SCQ: this.examData.SCQ.map((item) => {
            return { QID: item.QID, weight: item.weight };
          }),
          MCQ: this.examData.MCQ.map((item) => {
            return { QID: item.QID, weight: item.weight };
          }),
        };
        // [待處理]後端接收資料，計算加權分數，並回傳結果
        console.log(examResultSent);
        this.$http.post("api/questionExam/saveExamResult", examResultSent).then((res) => {
          console.log(res.data);
        });
        // 把ExamData存到vuex
        this.$store.commit("SET_EXAMINATION", this.examData);
        router.push("/examResult"); // 跳轉至考試結果頁面
      }
    },
  },
};
</script>

<style lang="scss">
p {
  margin: 0;
}
table {
  border: 5px double black;
  margin: auto;
  width: 100%;
}

td {
  border: 1px solid black;
}

select {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 20px;
  font-size: large;
}
</style>
